'use client';

import { useState, useEffect } from 'react';
import { Button } from "@/components/ui/button";
import {
  Card,
  CardContent,
  CardDescription,
  CardFooter,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Checkbox } from "@/components/ui/checkbox";
import { Switch } from "@/components/ui/switch";
import { Leaf, Phone, KeyRound, Mic, Loader2 } from "lucide-react";
import { useToast } from "@/hooks/use-toast";
import { useLanguage } from '@/contexts/language-context';
import Link from 'next/link';

export default function LoginPage() {
  const { language, setLanguage, t } = useLanguage();
  const [phone, setPhone] = useState('');
  const [otp, setOtp] = useState('');
  const [aadhaarId, setAadhaarId] = useState('');
  const [consent, setConsent] = useState(false);
  const [isOtpSent, setIsOtpSent] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [location, setLocation] = useState<GeolocationCoordinates | null>(null);

  const { toast } = useToast();

  useEffect(() => {
    // Mock GPS capture
    navigator.geolocation.getCurrentPosition(
      (position) => {
        setLocation(position.coords);
      },
      () => {
        console.warn("Could not get location. Mocking data for now.");
      }
    );
  }, []);

  const handleSendOtp = async (e: React.FormEvent) => {
    e.preventDefault();
    if (phone.length !== 10 || !/^\d+$/.test(phone)) {
      toast({ title: t('invalidPhone', 'login'), description: t('invalidPhoneDesc', 'login'), variant: 'destructive' });
      return;
    }
    setIsLoading(true);
    // Mock OTP sending
    await new Promise(resolve => setTimeout(resolve, 1000));
    setIsLoading(false);
    setIsOtpSent(true);
    toast({ title: t('otpSent', 'login'), description: t('otpSentDesc', 'login').replace('{phone}', phone) });
  };
  
  const handleLogin = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!consent) {
        toast({ title: t('consentRequired', 'login'), description: t('consentRequiredDesc', 'login'), variant: 'destructive' });
        return;
    }
    if (otp.length !== 4) {
        toast({ title: t('invalidOtp', 'login'), description: t('invalidOtpDesc', 'login'), variant: 'destructive' });
        return;
    }
    setIsLoading(true);
    // Mock login and data encryption
    console.log("Mock Encrypted Phone:", btoa(phone));
    if (aadhaarId) console.log("Mock Encrypted Aadhaar:", btoa(aadhaarId));
    console.log("Mock Location:", location);

    await new Promise(resolve => setTimeout(resolve, 1500));
    setIsLoading(false);
    toast({ title: t('loginSuccess', 'login') });
    // Redirect to dashboard
    window.location.href = '/dashboard';
  };
  
  const handleVoiceInstruction = () => {
    if ('speechSynthesis' in window) {
      const utterance = new SpeechSynthesisUtterance(t('voiceInstruction', 'login'));
      utterance.lang = language === 'en' ? 'en-US' : 'ml-IN';
      window.speechSynthesis.speak(utterance);
    } else {
      toast({ title: "Feature not supported", description: "Your browser does not support voice instructions.", variant: 'destructive'});
    }
  };


  return (
    <Card className="w-full max-w-md mx-auto bg-card/90 backdrop-blur-sm">
      <CardHeader className="text-center">
        <div className="flex justify-center items-center gap-4 mb-2">
            <Leaf className="h-10 w-10 text-primary" />
            <h1 className="text-2xl font-bold">{t('title', 'login')}</h1>
        </div>
        <CardDescription>{t('description', 'login')}</CardDescription>
        <div className="flex items-center justify-center space-x-2 pt-4">
            <span>English</span>
            <Switch
              checked={language === 'ml'}
              onCheckedChange={(checked) => setLanguage(checked ? 'ml' : 'en')}
            />
            <span>മലയാളം</span>
        </div>
      </CardHeader>
      <CardContent>
        <form onSubmit={isOtpSent ? handleLogin : handleSendOtp} className="grid gap-6">
          {!isOtpSent ? (
            <div className="grid gap-2">
              <Label htmlFor="phone" className="text-base">{t('phoneLabel', 'login')}</Label>
              <div className="flex gap-2">
                <Input
                  id="phone"
                  type="tel"
                  placeholder={t('phonePlaceholder', 'login')}
                  value={phone}
                  onChange={(e) => setPhone(e.target.value)}
                  required
                  className="h-14 text-lg"
                  maxLength={10}
                />
                <Button variant="ghost" size="icon" type="button" onClick={handleVoiceInstruction} className="h-14 w-14">
                    <Mic className="h-6 w-6"/>
                </Button>
              </div>
            </div>
          ) : (
            <>
                <div className="grid gap-2">
                    <Label htmlFor="otp" className="text-base">{t('otpLabel', 'login')}</Label>
                    <Input
                    id="otp"
                    type="text"
                    inputMode='numeric'
                    placeholder={t('otpPlaceholder', 'login')}
                    value={otp}
                    onChange={(e) => setOtp(e.target.value)}
                    required
                    className="h-14 text-lg tracking-[1em] text-center"
                    maxLength={4}
                    />
                </div>
                 <div className="grid gap-2">
                    <Label htmlFor="aadhaar" className="text-base">{t('aadhaarLabel', 'login')}</Label>
                    <Input
                    id="aadhaar"
                    type="text"
                    placeholder={t('aadhaarPlaceholder', 'login')}
                    value={aadhaarId}
                    onChange={(e) => setAadhaarId(e.target.value)}
                    className="h-14 text-lg"
                    />
                </div>
            </>
          )}

          <div className="flex items-start space-x-3">
              <Checkbox id="consent" checked={consent} onCheckedChange={(checked) => setConsent(!!checked)} className="mt-1 h-5 w-5"/>
              <Label htmlFor="consent" className="text-sm text-muted-foreground">{t('consentLabel', 'login')}</Label>
          </div>
          
          <Button type="submit" className="w-full h-14 text-lg" disabled={isLoading}>
            {isLoading ? <Loader2 className="animate-spin" /> : (isOtpSent ? t('login', 'login') : t('sendOtp', 'login'))}
          </Button>
        </form>
         <div className="mt-4 text-center text-sm">
            {t('noAccount', 'login')}{' '}
            <Link href="/register" className="underline">
                {t('registerNow', 'login')}
            </Link>
          </div>
      </CardContent>
      <CardFooter className="flex-col gap-4">
      </CardFooter>
    </Card>
  );
}
